import React, { useState, useEffect } from "react";

import Menu from "./Menu";
import DiagrammeGant from "./DiagrammeGant";
import AjouterInstance from "./AjouterInstance";
import "./App.css";

function Home(props) {
  const [parametre, setParametre] = useState({});
  const [afficherGant, setAfficherGant] = useState(false);
  const [afficherAjouterInstance, setAfficherAjouterInstance] = useState(false);

  return (
    <>
      <Menu
        setParams={setParametre}
        setAfficherAjouterInstance={setAfficherAjouterInstance}
        setAfficherGant={setAfficherGant}
      />

      {afficherAjouterInstance ? (
        <AjouterInstance
          setAfficherAjouterInstance={setAfficherAjouterInstance}
        />
      ) : (
        ""
      )}
      {afficherGant ? <DiagrammeGant parametre={parametre} /> : ""}
    </>
  );
}

export default Home;
