import React, { useEffect, useState, useRef } from "react";
import axios from "axios";
import Swal from "sweetalert2";

function Ajouterinstance(props) {
  const [formData, setFormData] = useState(new FormData());
  const [accept, setAccept] = useState(false);
  const handleSubmit = (e) => {
    e.preventDefault();

    if (!formData.problem) {
      Swal.fire({
        title: "Données manquantes",
        text: "Veulliez choisir un probème",
        icon: "error",
        confirmButtonText: "D'accord",
      });
      return;
    }

    if (!formData.nbTaches) {
      Swal.fire({
        title: "Données manquantes",
        text: "Veulliez Spécifier le nombre de tâches",
        icon: "error",
        confirmButtonText: "D'accord",
      });
      return;
    }

    if (!formData.fichierData) {
      console.log(formData);
      Swal.fire({
        title: "Données manquantes",
        text: "Veulliez choisir un fichier txt qui contient l'instance de votre ordinateur",
        icon: "error",
        confirmButtonText: "D'accord",
      });
      return;
    } else if (!accept) {
      console.log(formData);
      Swal.fire({
        title: "Type erroné",
        text: "Veulliez choisir un fichier txt qui contient l'instance de votre ordinateur",
        icon: "error",
        confirmButtonText: "D'accord",
      });
      return;
    }

    axios
      .post("https://lachmed27.pythonanywhere.com/addInstance", formData, {
        header: {
          "content-type": "multipart/form-data",
        },
      })
      .then((resp) => {
        if (resp.data == "success") {
          setAccept(false);
          props.setAfficherAjouterInstance(false);
          Swal.fire({
            title: "Fichier ajouté !",
            text: "Veulliez actualiser la page pour pouvoir séléctioner l'instance que vous venez d'ajouter ",
            icon: "success",
            confirmButtonText: "D'accord",
          });
        } else {
          setAccept(true);
          props.setAfficherAjouterInstance(true);
          Swal.fire({
            title: "Echec !",
            text: "Veulliez vérifier les données entrés et que les critères demandées sont prise en compte et réssayer",
            icon: "error",
            confirmButtonText: "D'accord",
          });
        }
      });
  };

  const handleFormData = (e) => {
    const target = e.target;

    const name = target.name;

    const value = name === "fichierData" ? target.files[0] : target.value;

    if (name === "fichierData") {
      let reader = new FileReader();

      reader.readAsText(value);

      reader.onload = () => {
        setFormData({ ...formData, [name]: reader.result });
        setAccept(true);
      };
    } else {
      setFormData({ ...formData, [name]: value });
    }
  };

  const critereP2 = [
    <li>
      La quatrième ligne doit contenir les pénalité d'avance (ai) séparés par
      des tabulations
    </li>,
    <li>
      La dernière ligne doit contenir les pénalité de retard (bi) séparés par
      des tabulations
    </li>,
  ];

  return (
    <>
      <div className="AjouterInstanceContainer container">
        <h1>Ajouter une instance</h1>
        <form onSubmit={(e) => handleSubmit(e)} encType="multipart/form-data">
          <div className="form-group">
            <input
              type="number"
              name="nbTaches"
              value={formData.nbTaches}
              onChange={(e) => handleFormData(e)}
              className="form-control inputInstance"
              placeholder="Nombre de tâches"
            />
          </div>
          <div className="form-group">
            <select
              name="problem"
              value={formData.problem}
              onChange={(e) => handleFormData(e)}
              className="form-control inputInstance"
            >
              <option value="" className="option btn">
                Problème
              </option>
              <option value="p1" className="option btn">
                Problème 1
              </option>
              <option value="p2" className="option btn">
                Problème 2
              </option>
            </select>
          </div>
          <div className="form-group fileGroup">
            <span className="inputInstance">
              Veuillez choisir un fichier text (.txt):{" "}
            </span>
            <input
              type="file"
              accept=".txt"
              name="fichierData"
              onChange={(e) => handleFormData(e)}
              className="form-control-file inputInstance"
              id="exampleFormControlInput1"
              placeholder="name@example.com"
            />
          </div>
          <div className="form-group">
            <div className="alert alert-warning">
              <h5>Merci de respecter ces critères: </h5>
              <ul>
                <li>La première ligne doit contenir le nombre des tâches</li>
                <li>
                  La deuxième ligne doit contenir les processing time (pi)
                  séparés par des tabulations
                </li>
                <li>
                  La troisième ligne doit contenir les due date (di) séparés par
                  des tabulations
                </li>
                {formData.problem === "p2" ? critereP2 : ""}
              </ul>
            </div>
          </div>
          <button type="submit" className="btn btn-primary">
            Ajouter
          </button>
          <button
            onClick={() => props.setAfficherAjouterInstance(false)}
            className="btn btn-primary"
          >
            Annuler
          </button>
        </form>
      </div>
    </>
  );
}

export default Ajouterinstance;
