import React, { useState, useEffect } from "react";
import axios from "axios";
import Swal from "sweetalert2";
import "./App.css";

function Menu(props) {
  const [formData, setFormData] = useState({});
  const [instances, setInstances] = useState({});

  useEffect(() => {
    axios
      .get("https://lachmed27.pythonanywhere.com/getInstances")
      .then((response) => {
        setInstances(response.data);
      });
  }, []);

  const handleForm = (e) => {
    const target = e.target;

    const name = target.name;
    const value = target.value;

    setFormData({
      ...formData,
      [name]: value,
    });
  };
  const tabOptions = [
    { value: "dyna", text: "Dynamique" },
    { value: "meta", text: "Metaheuristique" },
    { value: "poly", text: "Polynomiale" },
  ];

  const OptionsP1 = tabOptions.map((elm) => {
    return (
      <option value={elm.value} className="option btn">
        {elm.text}
      </option>
    );
  });
  const OptionsP2 = tabOptions.map((elm) => {
    if (elm.value != "poly")
      return (
        <option value={elm.value} className="option btn">
          {elm.text}
        </option>
      );
  });

  const nombresTachesP1 = Object.keys(instances).map((elm) => {
    if (elm == "P1") {
      const res = [];
      for (let i in instances[elm])
        res.push(
          <option value={instances[elm][i]} className="option btn">
            {" "}
            {instances[elm][i]}{" "}
          </option>
        );

      return res;
    }
  });

  const nombresTachesP2 = Object.keys(instances).map((elm) => {
    if (elm == "P2") {
      const res = [];
      for (let i in instances[elm])
        res.push(
          <option value={instances[elm][i]} className="option btn">
            {" "}
            {instances[elm][i]}{" "}
          </option>
        );
      return res;
    }
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!formData.problem) {
      Swal.fire({
        title: "Données manquantes",
        text: "Veulliez choisir un probème",
        icon: "error",
        confirmButtonText: "D'accord",
      });
      return;
    }

    if (!formData.methode) {
      Swal.fire({
        title: "Données manquantes",
        text: "Veulliez choisir une méthode",
        icon: "error",
        confirmButtonText: "D'accord",
      });
      return;
    }

    if (!formData.instance) {
      Swal.fire({
        title: "Données manquantes",
        text: "Veulliez choisir une instance",
        icon: "error",
        confirmButtonText: "D'accord",
      });
      return;
    }
    props.setAfficherGant(true);
    props.setParams(formData);
  };

  return (
    <>
      <section className="hero-area bg-1 text-center overly Menu">
        <div className="container">
          <div className="row">
            <div className="col-md-12 text">
              <div className="content-block">
                <h1>Ordannacement de taches sur machine critiques </h1>
                <p>
                  Pour minimiser une fonction objective en utilisant la
                  programmation dynamique, la métaheuristique et l'algorithme de
                  Hodson and Moore
                  <br /> Comparaison entre ces trois techniques
                </p>
              </div>
              <div className="advance-search">
                <div className="container">
                  <div className="row justify-content-center">
                    <div className="col-lg-12 col-md-12 align-content-center">
                      <form onSubmit={handleSubmit}>
                        <div className="form-row">
                          <div className="form-group col-md-2">
                            <select
                              name="problem"
                              onChange={handleForm}
                              className="w-100 form-control mt-lg-1 mt-md-2"
                            >
                              <option value="" className="option btn">
                                Problème
                              </option>
                              <option value="p1" className="option btn">
                                Problème 1
                              </option>
                              <option value="p2" className="option btn">
                                Problème 2
                              </option>
                            </select>{" "}
                          </div>
                          <div className="form-group col-md-3">
                            <select
                              name="methode"
                              onChange={handleForm}
                              className="w-100 form-control mt-lg-1 mt-md-2"
                            >
                              <option value="" className="option btn">
                                Methode
                              </option>
                              {formData.problem == "p2" ? OptionsP2 : OptionsP1}
                            </select>
                          </div>
                          <div className="form-group col-md-2">
                            <select
                              name="instance"
                              onChange={handleForm}
                              className="w-100 form-control mt-lg-1 mt-md-2"
                            >
                              <option value="" className="option btn">
                                Instance
                              </option>
                              {formData.problem == "p2"
                                ? nombresTachesP2
                                : nombresTachesP1}
                            </select>{" "}
                          </div>
                          <div className="form-group col-md-2 align-self-center">
                            <button
                              type="submit"
                              className="btn-primary w-100 form-control mt-lg-1 mt-md-2"
                            >
                              Voir résultat
                            </button>
                          </div>
                          <div className="form-group col-md-3 align-self-center">
                            <button
                              type="button"
                              onClick={() =>
                                props.setAfficherAjouterInstance(true)
                              }
                              className="btn-primary w-100 form-control mt-lg-1 mt-md-2"
                            >
                              Ajouter une instacne
                            </button>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default Menu;

