import React from 'react'
import {Link} from "react-router-dom";

function Header(props) {
    

    return (
        <>
            <section>
	<div className="container header">
		<div className="row">
			<div className="col-md-12">
				<nav className="d-flex justify-content-between  navbar navbar-expand-lg navbar-light navigation">
					<Link to="/">
						<img className='logo' src="https://raw.githubusercontent.com/lachmed/SingleMachineScheduling/master/logo.png?token=AM2TNPMF6CUHTAN26YV3UJ3BZQP3Q" />
					</Link>
					<div className="navbar" >
						<ul className="navbar-nav ml-auto main-nav ">
							<li className="nav-item ">
                            <Link to="/">
                                <span className="nav-link btn" >Accueil</span>
                            </Link>
							</li>
							
							<li className="nav-item ">
                            <Link to="/AboutUs">
                                <span className="nav-link btn" >A propos</span>
                            </Link>
							</li>
							
                            <li className="nav-item ">
                            <Link to="/Rapport">
                                <span className="nav-link btn" >Rapport</span>
                            </Link>
							</li>
						</ul>
						
					</div>
				</nav>
			</div>
		</div>
	</div>
</section>
        </>
    )
}

export default Header