import React from 'react'
import {Link} from 'react-router-dom'
function Error(props) {
    

    return (
        <>
        <div className='Error'>
          <h1>Oops, Rien à voir ici !!</h1>
          <Link to="/">
            Aller à la page d'accueil
          </Link>
        </div>
          
        </>
    )
}


export default Error