import axios from "axios";
import React, { useEffect, useState } from "react";
import Swal from "sweetalert2";

import Gant from "./Gant";
import "./App.css";

function Diagrammegant(props) {
  const [sequance, setSequance] = useState([]);
  const [finnished, setFinnished] = useState(-1);
  const [valeurMin, setValeurMin] = useState(-1);
  const [temps, setTemps] = useState(0);
  const [data, setData] = useState({});
  const [done, setDone] = useState(false);

  useEffect(() => {
    if (
      props.parametre.problem == "p1" &&
      props.parametre.methode == "dyna" &&
      parseInt(props.parametre.instance.substring(4)) > 21
    ) {
      props.parametre.methode = "poly";
      Swal.fire({
        title:
          "La programmation Dynamique ne peut pas résoudre cette instance | Passage vers La méthode Polynomiale",
        text: "Maximum de tache qui peuvent être ordonanncé par la programmation dynamique est 21,On va résoudre cette instance par la méthode polynomiale",
        icon: "info",
        confirmButtonText: "D'accord",
      });
      setDone(false);
      axios
        .get(
          "https://lachmed27.pythonanywhere.com/poly?problem=" +
            props.parametre.problem +
            "&instance=" +
            props.parametre.instance
        )
        .then((response) => extraireSequance(response.data));

      return;
    }
    if (
      props.parametre.problem == "p2" &&
      props.parametre.methode == "dyna" &&
      parseInt(props.parametre.instance.substring(4)) > 21
    ) {
      props.parametre.methode = "meta";

      Swal.fire({
        title:
          "La programmation Dynamique ne peut pas résoudre cette instance| Passage vers La métaheuristique",
        text: "Maximum de tache qui peuvent être ordonanncé par la programmation dynamique est 21,On va résoudre cette instance par la méthode metaheuristique",
        icon: "info",
        confirmButtonText: "D'accord",
      });

      setDone(false);
      axios
        .get(
          "https://lachmed27.pythonanywhere.com/meta?problem=" +
            props.parametre.problem +
            "&instance=" +
            props.parametre.instance
        )
        .then((response) => extraireSequance(response.data));

      return;
    }

    if (props.parametre.methode == "dyna") {
      setDone(false);
      axios
        .get(
          "https://lachmed27.pythonanywhere.com/" +
            props.parametre.methode +
            "?problem=" +
            props.parametre.problem +
            "&instance=" +
            props.parametre.instance
        )
        .then((response) => {
          extraireSequance(response.data);
        });

      return;
    }

    if (props.parametre.methode == "poly") {
      setDone(false);
      axios
        .get(
          "https://lachmed27.pythonanywhere.com/" +
            props.parametre.methode +
            "?problem=" +
            props.parametre.problem +
            "&instance=" +
            props.parametre.instance
        )
        .then((response) => {
          extraireSequance(response.data);
        });

      return;
    }

    if (props.parametre.methode == "meta") {
      setDone(false);
      axios
        .get(
          "https://lachmed27.pythonanywhere.com/" +
            props.parametre.methode +
            "?problem=" +
            props.parametre.problem +
            "&instance=" +
            props.parametre.instance
        )
        .then((response) => {
          extraireSequance(response.data);
        });

      return;
    }
  }, [props.parametre]);

  const extraireSequance = (resultat) => {
    if (props.parametre.methode == "poly") {
      const liste = resultat.sequance;
      const tmpsequance = [];
      for (let i in liste) {
        tmpsequance.push(liste[i][0]);
      }

      setSequance(tmpsequance);
      setValeurMin(resultat.valeur);
      setTemps(resultat.temps);
      setData(resultat.data);
      setDone(true);

      return;
    }
    if (props.parametre.methode == "dyna") {
      setSequance(resultat.sequance[2]);
      setFinnished(resultat.sequance[0]);
      setValeurMin(resultat.sequance[1]);
      setTemps(resultat.sequance[4]);
      setData(resultat.sequance[5]);
      setDone(true);
      return;
    }

    if (props.parametre.methode == "meta") {
      setSequance(resultat.sequance.sequance);
      setValeurMin(resultat.sequance.valeur);
      setTemps(resultat.sequance.temps);
      setData(resultat.sequance.data);
      setDone(true);
      return;
    }
  };

  return (
    <>
      {" "}
      <div className="container">
        <Gant
          methode={props.parametre.methode}
          done={done}
          sequance={sequance}
          finnished={finnished}
          valeurMin={valeurMin}
          temps={temps}
          data={data}
        />
      </div>
    </>
  );
}

export default Diagrammegant;
