import React, { useEffect, useState } from 'react'


import './App.css'

function Gant(props) {

    

    const jobs = props.sequance.map((job, index) => {
            
        return <>  
            {index==0 ? <span className='circle'>0</span> : ""}
            <span  title={job}  className='job btn btn-primary' style={{
            backgroundColor: "#"+Math.floor((job/(props.sequance.length+1))*16777215).toString(16),
          }}> {job} { index<props.sequance.length-1 ?  <i className="fas fa-arrow-circle-right"></i> : ""} </span > <span className='circle'>{props.data[index]}</span>
          
          
          
           
          
            </>

    })


    return (
        <>
        <div className='gant container'>
            
            { props.done === true ?
                <div className="container">
                    <div className='bar1 barInfo container alert alert-primary d-flex justify-content-around'>
                    <h2>Valeur minimale</h2>
                        {props.valeurMin != -1 ?  <h3> {props.valeurMin}</h3> : ""}
                    <h2>Temps d'exécution </h2>
                    {props.temps != 0 ?  <h3> {  props.temps.toFixed(4) } secondes</h3> : ""}
                    
                    </div>
                    
                    <h2 className='barInfo bar1'>(Une) Séquance optimale</h2>
                        {jobs}
                    
                </div>
            : 
            <center>
            <div className='loading'>
            <div className=" spinner-border text-primary" role="status">
                <span className="sr-only">Loading...</span>
            </div>
            <h3>Veuillez attendre la fin de l'éxecution</h3>
            </div>
            </center>
            } 
        </div> 
        </>
    )
}


export default Gant 