import React,{useState} from 'react'

import "./App.css"

function Member(props){

    return( <>
        <div className="container member">
        
        <div className="card">
            <img src={ props.info.image } alt={props.info.name} style={{width:100+"%"}}/>
        <div className="container">
            <h2>{props.info.name}</h2>
            <p className="title">Etudiant en M2SI </p>
            <p>21 ans </p>
            <p>{props.info.email}</p>
        <p>
            <a className="button" target="blank" href={props.info.linkedin}>LinkedIn</a>
        </p>
        </div>
    
  </div>

   </div>
  </>
    )

}




function Aboutus(props) {
    
    const [lachmed, setLachMed] = useState({
            image : "https://raw.githubusercontent.com/lachmed/Transport/master/lachmed.jpg",
            name: "Lachgar Mohamed",
            description: "21 years old computer science student at INSEA-M2SI",
            linkedin: "https://www.linkedin.com/in/mohamed-lachgar-a97563163/",
            email: "mlachgar@insea.ac.ma"
    })

    const [oDaif, setoDaif] = useState({
        image : "https://media-exp1.licdn.com/dms/image/C4E03AQHbiFtzqhHqgg/profile-displayphoto-shrink_800_800/0/1638626117750?e=1645660800&v=beta&t=25JTLxu78Nb_0TjZBDACfxqp3MoMAqV0HzB85q8nbiQ",
        name: "Daif Othmane",
        description: " computer science student at INSEA-M2SI",
        linkedin: "https://www.linkedin.com/in/daifothmane/",
        email: "odaif@insea.ac.ma"
    })

    return (
        <>
        <h1 className='AboutUsTitle'>
            Ce projet est réalisé par :
        </h1>
        <center className="container membersContainer d-flex justify-content-around">
            <Member info={lachmed}/>
            <Member info={oDaif} />
        </center>
        </>
    )
}


export default Aboutus